import React from 'react';
import PropTypes from 'prop-types';

// hero is ALWAYS fullheight. let's change this so it's hidden on mobile

const Hero = ({ children, classes = '' }) => (
  <div>
    {/* hide on mobile */}
    {/* <section className={`hero ${classes} is-hidden-mobile`}>
      {children}
    </section>
    {/* show on everything that is not mobile */}
    {/*<section className="hero is-hidden-tablet">
      {children}
    </section> */}
    <section className={`hero ${classes}`}>
      {children}
    </section>
  </div>
);

Hero.propTypes = {
  children: PropTypes.node.isRequired,
  classes: PropTypes.string.isRequired
};

export default Hero;


